import React, { forwardRef, useRef, useState } from 'react';
import { Container, Row, Col } from 'react-grid-system';

import { useScaledProp } from 'react-scaled-props';

const handleCursor = {
  both: 'nwse-resize',
  height: 'ns-resize',
  width: 'ew-resize',
};

const handlePosition = {
  both: { bottom: -10, right: -10 },
  width: { top: 'calc(50% - 10px)', right: -10 },
  height: { bottom: -10, left: 'calc(50% - 10px)' },
};

const ResizeableBox = forwardRef((props, ref) => {
  const {
    color,
    resizeable,
    infoDisplay = '',
    initialHeight,
    initialWidth,
    minHeight,
    maxHeight,
    minWidth,
    maxWidth,
    children,
    style,
    ...restProps
  } = props;

  const [size, setSize] = useState({ height: initialHeight, width: initialWidth });

  const handleDrag = e => {
    if (ref.current === null) {
      return;
    }

    const scrollLeft = window.pageXOffset || document.documentElement.scrollLeft;
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

    const elBoundingRect = ref.current.getBoundingClientRect();

    const { top, left } = elBoundingRect;

    const x = e instanceof TouchEvent ? e.touches[0].pageX : e.pageX;
    const y = e instanceof TouchEvent ? e.touches[0].pageY : e.pageY;

    let newWidth = x - (left + scrollLeft);
    let newHeight = y - (top + scrollTop);

    // hack to avoid final event (on mouse up) where pageX & pageY return 0
    if (newWidth === 0 && newHeight === 0) {
      return;
    }

    if (newWidth < minWidth) {
      newWidth = minWidth;
    }

    if (newWidth > maxWidth) {
      newWidth = maxWidth;
    }

    if (newHeight < minHeight) {
      newHeight = minHeight;
    }

    if (newHeight > maxHeight) {
      newHeight = maxHeight;
    }

    setSize({
      height: newHeight,
      width: newWidth,
    });
  };

  const handleDragStop = () => {
    window.document.getElementsByTagName('body')[0].style.cursor = 'auto';
    window.removeEventListener('mousemove', handleDrag);
    window.removeEventListener('touchmove', handleDrag);
    window.removeEventListener('mouseup', handleDragStop);
    window.removeEventListener('touchend', handleDragStop);
  };

  const handleDragStart = e => {
    window.document.getElementsByTagName('body')[0].style.cursor = handleCursor[resizeable];
    window.addEventListener('mousemove', handleDrag);
    window.addEventListener('touchmove', handleDrag);
    window.addEventListener('mouseup', handleDragStop);
    window.addEventListener('touchend', handleDragStop);
  };

  const { height, width } = size;

  return (
    <div
      ref={ref}
      style={{
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        height,
        width,
        backgroundColor: color,
        userSelect: 'none', // disable selection of text
        margin: 10,
        maxWidth: 'calc(100vw - 40px - 20px)', // hack to prevent overflow on mobile
        ...style,
      }}
      {...restProps}
    >
      <div style={{ position: 'absolute', top: 10, left: 10 }}>
        <div style={{ marginBottom: 10 }}>
          {resizeable === 'width' && `width: ${width.toFixed(0)}px`}
          {resizeable === 'height' && `height: ${height.toFixed(0)}px`}
          {resizeable === 'both' && `${width.toFixed(0)}px x ${height.toFixed(0)}px`}
        </div>
        {infoDisplay}
      </div>
      {children}
      <div
        onTouchStart={handleDragStart}
        onMouseDown={handleDragStart}
        style={{
          position: 'absolute',
          ...handlePosition[resizeable],
          cursor: handleCursor[resizeable],
          height: 20,
          width: 20,
          borderRadius: '50%',
          backgroundColor: 'white',
          border: '2px solid lightblue',
          boxSizing: 'border-box',
          touchAction: 'none', // don't scroll when dragging this el
        }}
      />
    </div>
  );
});

export default function App() {
  const fontSizeElRef = useRef(null);
  const opacityElRef = useRef(null);
  const colorElRef = useRef(null);

  const minWidth = 200;
  const minHeight = 150;
  const maxWidth = 400;
  const maxHeight = 300;

  const fontSize = useScaledProp(fontSizeElRef, minWidth, maxWidth, { minValue: 8, maxValue: 17 });
  const opacity = useScaledProp(opacityElRef, minHeight, maxHeight, {
    minValue: 0.1,
    maxValue: 1,
    scaledBy: 'height',
  });

  const hue = useScaledProp(colorElRef, minWidth, maxWidth, { minValue: 0, maxValue: 255 });
  const lightness = useScaledProp(colorElRef, minHeight, maxHeight, {
    minValue: 50,
    maxValue: 75,
    scaledBy: 'height',
  });

  return (
    <Container>
      <Row>
        <Col span={12}>
          <h1>
            <span role="img" aria-label="Ruler emoji">
              
            </span>{' '}
            react-scaled-props
          </h1>
        </Col>
      </Row>
      <Row>
        <Col span={6} xs={12}>
          <pre>
            {`
const fontSize = useScaledProp(
  ref,
  'width',
  {
    minSize: ${minWidth},
    maxSize: ${maxWidth},
    minValue: 8,
    maxValue: 17
  }
)`}
          </pre>
        </Col>
        <Col span={6} xs={12}>
          <ResizeableBox
            ref={fontSizeElRef}
            resizeable={'width'}
            infoDisplay={`fontSize: ${fontSize.toFixed(1)}`}
            color="papayawhip"
            initialHeight={minHeight}
            initialWidth={(maxWidth + minWidth) / 2}
            minHeight={minHeight}
            minWidth={minWidth}
            maxHeight={minHeight}
            maxWidth={maxWidth}
          >
            <div style={{ fontSize, padding: 10 }}>
              This text will shrink and grow with the div's <strong>width</strong>
            </div>
          </ResizeableBox>
        </Col>
      </Row>
      <Row>
        <Col span={6} xs={12}>
          <pre>
            {`
const opacity = useScaledProp(
  ref,
  'height',
  {
    minSize: ${minHeight},
    maxSize: ${maxHeight},
    minValue: 0.1,
    maxValue: 1
  }
)
`}
          </pre>
        </Col>
        <Col span={6} xs={12}>
          <ResizeableBox
            ref={opacityElRef}
            resizeable={'height'}
            infoDisplay={`opacity: ${opacity.toFixed(2)}`}
            color="palevioletred"
            initialHeight={(maxHeight + minHeight) / 2}
            initialWidth={(maxWidth + minWidth) / 2}
            minHeight={minHeight}
            minWidth={(maxWidth + minWidth) / 2}
            maxHeight={maxHeight}
            maxWidth={(maxWidth + minWidth) / 2}
          >
            <div style={{ opacity, padding: 10 }}>
              This text will fade in and out with the div's <strong>height</strong>
            </div>
          </ResizeableBox>
        </Col>
      </Row>
      <Row>
        <Col span={6} xs={12}>
          <pre>
            {`
const hue = useScaledProp(
  ref,
  'width',
  {
    minSize: ${minWidth},
    maxSize: ${maxWidth},
    minValue: 0,
    maxValue: 255
  }
);

const lightness = useScaledProp(
  ref,
  'height',
  {
    minSize: ${minHeight},
    maxSize: ${maxHeight},
    minValue: 50,
    maxValue: 75
  }
);
`}
          </pre>
        </Col>
        <Col span={6} xs={12}>
          <ResizeableBox
            ref={colorElRef}
            resizeable={'both'}
            infoDisplay={`color: hsl(${hue.toFixed(0)}, 50%, ${lightness.toFixed(0)}%)`}
            color={`hsl(${hue}, 50%, ${lightness}%)`}
            initialHeight={(maxHeight + minHeight) / 2}
            initialWidth={(maxWidth + minWidth) / 2}
            minHeight={minHeight}
            minWidth={minWidth}
            maxHeight={maxHeight}
            maxWidth={maxWidth}
          />
        </Col>
      </Row>
      {/* add some padding to the bottom of the container */}
      <div style={{ height: 100 }} />
    </Container>
  );
}



